* {
  font-family: "Big Shoulders Text";
  letter-spacing: 3px;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background-color: #e8e1d9;
}
h2 {
  color: #f49d08;
  margin: 0;
  margin-bottom: 10px;
  padding: 8px;
  text-transform: uppercase;
  font-size: 50px;
}
table {
  border-collapse: collapse;
  width: 100%;

  font-size: 14px;
}
.stats {
  display: flex;
}

.stats div:first-child {
  flex: 1;
}
h3 {
  color: white;
  text-transform: uppercase;
  margin-bottom: 10px;
}
th,
td {
  text-align: left;
  padding: 6px;
  font-size: 20px;
  white-space: nowrap;
}
th {
  text-transform: uppercase;
}
.stats td,
.stats th {
  padding: 4px 8px;
}

td.name {
  letter-spacing: 2px;
}
td img {
  margin-right: 10px;
}

a {
  color: #f2f2f2;
}

.container {
  max-width: 1920px;
  max-height: 1080px;
  display: flex;
}

.leftCol {
  width: 625px;
  text-align: center;
  margin-top: 26px;
}

.rightColTop {
  height: 80px;
  color: #f2f2f2;
  padding: 10px 20px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  font-size: 35px;
}

#points {
  flex: 1;
  padding: 0 20px;
}

.rightCol {
  flex: 1;
  display: flex;
  flex-flow: column;
}

.sponsors {
  display: flex;
  color: #f2f2f2;
  align-items: center;
  justify-content: center;
  padding: 0 0 20px 0;
  height: 60px;
  background: rgba(0, 0, 0, 0.5);
  padding: 10px;
}

.sponsors img {
  margin-left: 20px;
}

.top {
  border-left: 4px solid rgb(116, 246, 125) !important;
}
.bottom:nth-child(odd) {
  border-left: 4px solid rgb(236, 69, 59) !important;
}
.bottom:nth-child(even) {
  border-left: 4px solid rgb(235, 50, 35) !important;
}
.kills {
  font-size: 16px !important;
}
